import React from 'react';
import ReactDOM from 'react-dom';
import { HighlightWithinTextarea } from 'react-highlight-within-textarea'
import './index.css';


class CloudForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    	text_to_generate: "Напишите начало",
    	receivedMessage: "Text to added"
    };
    this.mainInput = {};
  }

  handleChange = (event) => {
    this.setState({[event.target.name]: event.target.value});
  }

  handleSubmit = (event) => {
   	console.log("Text prompt: ", {"text": JSON.stringify(this.state.text_to_generate)});

    fetch('https://api.aicloud.sbercloud.ru/public/v1/public_inference/docs', {
        method: 'POST',
        body: {"text": JSON.stringify(this.state.text_to_generate)}, //JSON.stringify(this.state),
        mode: 'no-cors'
      }).then(function(response) {
        console.log("response: ", response)
        // return response.json()["predictions"];
        // this.state.receivedMessage = response.json()["predictions"];
      });

    const finaloutput = this.state.text_to_generate + "\nRuGPT3: " + this.state.receivedMessage;
    event.preventDefault();
	this.mainInput.value = finaloutput;
	console.log(this.state.receivedMessage);
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit} id="demoform" class="w-100 justify-content-center">
        <div>
        	<p class="text-center"><b>Введите текст:</b></p>
        </div>
        <div class="row">
          <textarea 
            ref={(ref) => this.mainInput= ref}
            value={this.state.value}
          	name="text_to_generate" 
          	onChange={this.handleChange} 
          	cols="100" rows="15"
          	>
          	</textarea>
        </div>

        <div class="row">
        	<input type="submit" value="Дополнить" class="btn btn-success" />
        </div>	
      </form>
    );
  }
}


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = "На словах ты Лев Толстой, а на деле ";
  }

render() {
  return (
    <div className="App">
      <header className="App-header">

        <CloudForm />

      </header>
    </div>
  );
}
}

// ========================================

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);

